.header {
  &__menu {
    position: absolute;
    top: 73px;
    left: 0;
    z-index: 1;
    min-height: 40px;
    width: 100%;
  }

  &__nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
    align-items: flex-start;
  }
}
.nav {
  &__logo {
    position: absolute;
    top: -55px;
    left: calc(50% - 142px);
    z-index: 3;
  }

  &__logo-img {
    width: 284px;
    background-size: cover;
  }

  &__menu-left,
  &__menu-right {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 38%;
    min-height: 40px;
    border-bottom: 1px solid #77470b;
    border-radius: 5px;
  }
}

.nav-list {
  &__item {
    background-image: linear-gradient(
      0deg,
      #a4671f 0%,
      #834c0b 10%,
      #77470b 30%,
      #b0782b 61%,
      #e9c087 100%
    );
    height: 40px;
    width: 33.33%;

    text-transform: uppercase;
    border: 1px solid #d4b999;
    border-right: 1px solid #77470b;

    padding: 0 10px;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: all 0.7s;
  }

  &__item:hover {
    background-color: #d7c074;
    background-image: linear-gradient(
      0deg,
      #faedc7 0%,
      #eacf8f 13%,
      #e8c478 63%,
      #fff9e0 100%
    );
  }

  &__item:first-child {
  }
  &__item:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-right: none;
  }

  &__link {
    transition: all 0.7s;
    color: #ffffff;
    font-family: Verdana, sans-serif;
    font-size: 11px;
    line-height: 1.2;
    text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
    word-wrap: break-word;
    text-align: center;
  }

  &__link-logo {
    width: 180px;
  }
}

.nav__menu-left .nav-list__item:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.dropdown-list {
  width: 190px;

  background-color: #fcebc6;
  border: 1px solid #bfa485;
  border-top: none;

  position: absolute;
  top: 41px;
  left: 123px;
  z-index: 5;

  word-wrap: break-word;
  text-align: left;
  text-transform: capitalize;
  display: none;

  &__item {
    padding: 5px 11px;
    transition: all 0.3s;
  }
  &__item:hover {
    background-color: #d8af75;
    transition: all 0.5s;
  }

  &__link {
    font-family: Verdana, sans-serif;
    font-size: 14px;
    line-height: 15px;
    color: #45250a;
  }
}

.nav-list__item:hover a.nav-list__link {
  color: #4e2c1e;
}
