.header {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;

    padding: 8px 20px 39px;

    background-color: #4e2c1e;

    min-height: 104px;
  }

  &__tel-container {
    padding-top: 12px;
  }

  &__top a {
    font-family: Book Antiqua;
    font-size: 26px;
    color: #ffffff;
    word-spacing: 5px;
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;

    position: relative;
    transition: all 0.7s;
  }

  &__button {
    font-family: bookantiqua_bold;
    font-size: 16px;

    width: 175px;
    min-height: 24px;
    background-color: #d7c074;
    background-image: linear-gradient(
      0deg,
      #faedc7 0%,
      #eacf8f 13%,
      #e8c478 63%,
      #fff9e0 100%
    );

    color: #472800;

    border: 1px solid #d8c992;
    border-radius: 5px;

    transition: all 0.7s;
    cursor: pointer;
  }

  &__button:hover {
    background-image: linear-gradient(
      0deg,
      #faedc7 0%,
      #77470b 72%,
      #d39538 100%
    );
    text-shadow: 0px 0.5px 0 rgba(17, 17, 26, 0.46);
    color: #ffffff;

    border: 1px solid #ab6e17;
  }
}
