.search {
  &__container {
    background-color: #fff7e1;
    background-image: url("../../img/index/bg.png");
    padding: 30px 0;
    text-align: center;
  }

  &__title {
    font-family: Book Antiqua;
    font-size: 25px;
    margin-bottom: 35px;
  }

  &__filter {
    max-width: 864px;
    display: flex;
    gap: 58px;

    align-items: center;

    background-color: rgba(190, 137, 63, 0.14);
    border: 1px solid #c4c0a5;
    border-radius: 2px;

    font-family: Verdana, sans-serif;
    font-weight: 500;

    margin: 0 auto;
    padding: 17px 47px;

    position: relative;
  }

  &__filter::before {
    content: "";
    background-image: url("../img/staff/decor-before.png");
    top: -11px;
    left: -11px;
    width: 66px;
    height: 67px;
    position: absolute;
  }
  &__filter::after {
    content: "";
    background-image: url("../img/staff/decor-after.png");
    top: -11px;
    right: -11px;
    width: 66px;
    height: 67px;
    position: absolute;
  }

  &__button {
    font-family: Verdana, sans-serif;
    font-size: 14px;
    text-transform: uppercase;
    color: #ffffff;

    padding: 3px 26px;


    background: linear-gradient(
      to top,
      rgb(164, 103, 31) 0%,
      rgb(131, 76, 11) 14%,
      rgb(119, 71, 11) 25%,
      rgb(176, 120, 43) 55%,
      rgb(233, 192, 135) 100%
    );
    box-shadow: 0px 2px 0px 0px rgba(126, 79, 19, 0.004);
    border: 1px solid #ab6e17;
    border-radius: 4px;

    transition: all 0.3s;
    cursor: pointer;
  }

  &__button:hover {
    background: linear-gradient(
      to top,
      rgb(250, 237, 199) 0%,
      rgb(234, 207, 143) 13%,
      rgb(232, 196, 120) 58%,
      rgb(255, 249, 224) 96%,
      rgb(255, 249, 224) 100%
    );
    color: #45250a;
  }
}

.left__text {
  font-family: Verdana, sans-serif;
  font-size: 18px;
}

.right__top,
.right__bottom {
  display: flex;
  gap: 10px;
}

.filter {
  &__left {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 18px;
  }

  &__text {
    font-size: 18px;
    word-spacing: 10px;
  }

  &__right {
    font-size: 18px;
    min-width: 250px;
    display: flex;
    flex-direction: column;
    align-items: end;
    gap: 9px;
  }

  &__input {
    padding: 9px 9px 9x 7px;
    width: 39px;
    height: 31px;
    border: 1px solid #b1b1b1;
    text-align: center;
    color: #45250a;
    font-size: 18px;
    line-height: 18px;
  }
}

.select {
  margin: 0 auto;
  position: relative;
  width: 203px;
  height: 32px;

  &__header {
    display: flex;
    align-items: center;
    position: relative;
    border: 1px solid #ab6e17;
    background-color: #fff;
    text-align: left;
    font-size: 18px;
    line-height: 20px;
    padding-left: 6px;
    z-index: 2;
    cursor: pointer;
  }

  &__header:focus {
    outline: auto;
  }

  &__current {
    font-size: 18px;
    line-height: 24px;
  }

  &__icon {
    background-image: url("../img/staff/select.png");
    background-repeat: no-repeat;
    background-position: right;
    flex-shrink: 0;
    margin-left: auto;
    width: 27px;
    height: 30px;
  }

  &__body {
    background-color: #d8af75;
    border: 1px solid #b1b1b1;
    overflow: hidden;
    width: 230px;

    display: none;
    left: 0;
    position: absolute;
    top: 0;
    z-index: 1;
    cursor: pointer;
  }

  &__item {
    height: 30px;
    line-height: 28px;
    background-color: #fcebc6;
    padding: 2px 12px;
    font-size: 18px;
    color: #45250a;
    transition: all 0.3s;
    width: 230px;
    cursor: pointer;
    text-align: left;
  }
  &__item:hover {
    background-color: #d8af75;
    transition: all 0.3s;
  }
  &__item:first-child:hover {
    background-color: #fcebc6;
  }
}






