
.personal {
  &__wrapper {
    max-width: 925px;
    min-height: 300px;
    background-color: #fff7e1;
    background-image: url("../../img/index/bg.png");
    padding: 40px 20px 0;
  }

  &__text {
    display: flex;
    flex-direction: column;
    padding-bottom: 100px;
  }
  &__wrapper  p{
    font-size: 14px;
    color: #4e2c1e;
    margin-bottom: 22px;

    text-align: justify;
    word-spacing: 11px;
    line-height: 1.1;

  }
  &__wrapper  p:last-of-type{
    margin-bottom: 0;
    word-spacing: 0px;
    text-align-last: justify;
  }
}







