.personal {
  &__info {
    max-width: 891px;
    min-height: 285px;
    margin: 0 auto 75px;
    padding-top: 30px;

    position: relative;

    display: flex;
    justify-content: space-around;
    align-items: center;

  }
}

.decor {
  &__container {
    position: absolute;
    width: 100%;
    height: 100%;


    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
  }
  &__top {
    width: 110px;
    height: auto;
  }
  &__line {
    width: 1px;
    background-color: #dfd2c0;
    height: calc(100% - 110px);
    margin-top: 55px;
    margin-bottom: 55px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  &__bottom {
    height: auto;
   }
}

.info {
  display: flex;
  justify-content: space-around;
  align-items: start;
  gap: 58px;
  position: relative;
}
.info .right-info {
  text-align: right;
}

.left-info,
.right-info {
  max-width: 374px;
  z-index: 1;

  flex: 1;
}

.left-info h3,
.right-info h3 {
  font-family: garamondC_bold;
  font-size: 23px;
  color: #5e3625;
  max-width: 200px;
  line-height: 24px;
  padding: 20px 0px;
  display: inline-block;
}

.left-info img,
.right-info img {
  width: 165px;
  height: 165px;
  object-fit: cover;
  shape-outside: element();
}

.left-info p,
.right-info p {
  margin-bottom: 5px;
  text-align: justify;
}

.left-info p:last-of-type,
.right-info p:last-of-type {
  margin-bottom: 25px;
}

.right-info h3 {
  width: 100%;
  text-align: right;
}
.right-info p {
  text-align: justify;
}

.left-info button,
.right-info button {
  width: 126px;
  min-height: 25px;
  background: linear-gradient(
    to top,
    rgb(250, 237, 199) 0%,
    rgb(234, 207, 143) 13%,
    rgb(232, 196, 120) 58%,
    rgb(255, 249, 224) 96%,
    rgb(255, 249, 224) 100%
  );
  color: #45250a;
  border: 1px solid #c7a865;
  border-radius: 4px;
  display: block;
  transition: all 0.7s;
  font-weight: 700;
  cursor: pointer;
}

.left-info img {
  float: right;
  padding: 25px 0px 12px 18px;
}

.left-info button {
  position: absolute;
  right: calc(50% + 28px);
  bottom: -10px;
}

.right-info button {
  position: absolute;
  left: calc(50% + 28px);
  bottom: -10px;
}


.right-info img {
  float: left;
  padding: 15px 23px 8px 10px;
}

.right-info button {
  margin-right: auto;
}

.left-info button:hover,
.right-info button:hover {
  background: linear-gradient(
    to top,
    rgb(164, 103, 31) 0%,
    rgb(131, 76, 11) 14%,
    rgb(119, 71, 11) 25%,
    rgb(176, 120, 43) 55%,
    rgb(233, 192, 135) 100%
  );
  color: #fff;
  border: 1px solid #ccc;
  transition: all 0.5s;
}
