.footer {
  width: 924px;
  min-height: 89px;
  background-color: #4e2c1e;
  background-image: linear-gradient(0deg, #ad8b63 0%, #4e2c1e 100%);

  display: flex;
  flex-direction: column;

  &__top {
    background-color: #4e2c1e;
    background-image: linear-gradient(0deg, #4e2c1e 0%, #4e2c1e 100%);
    color: #ffffff;
    font-size: 11px;
    text-transform: uppercase;
    text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
    box-shadow: 0px 1px 1px 0px rgba(17, 17, 26, 0.86);
    border-top: 1px solid #62483a;
  }

  &__list {
    min-height: 43px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__item a {
    display: block;
    position: relative;
    font-family: Arial, sans-serif;
    font-size: 11px;
    color: #ffffff;
    text-transform: uppercase;
    text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
    line-height: 11px;
    min-height: 43px;
    padding: 16px 40px;
  }

  &__item a::after {
    content: "";
    position: absolute;
    top: calc(50% - 5px);
    right: 0px;
    width: 1px;
    height: 10px;
    background-color: #ffffff;
  }

  &__item:nth-child(6n) a::after {
    content: none;
  }
  &__item:last-child a::after {
    content: none;
  }

  &__bottom {
    background: linear-gradient(
      to bottom,
      rgb(127, 93, 66) 0%,
      rgb(166, 132, 94) 100%
    );
    color: #ffffff;

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
    height: 100%;
  }
  &__tel {
    font-family: GaramondC;
  }
  &__tel a {
    font-size: 19px;
    color: #ffffff;
  }

  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__logo img {
    width: 70px;
    height: 26px;
    object-fit: cover;
  }

  &__logo p {
    font-family: Tahoma, sans-serif;
    font-size: 11px;
    text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.62);
  }
}
