.slider {
  display: flex;
  flex-direction: column;
  margin-top: 9px;

  &__container {
    min-width: 923px;
    min-height: 325px;
    display: flex;
    align-items: center;
    position: relative;
    filter: drop-shadow(0px 3px 2px rgba(0, 0, 0, 0.3));
    overflow: hidden;
  }

  &__images {
    display: flex;
    align-items: center;
    transition: all 1s ease;
  }

  &__images img {
    width: 923px;
    height: 324px;
    background-size: cover;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 19px;
    min-height: 30px;
    padding-top: 7px;
    background-color: #fff7e1;
    background-image: url("../../img/index/bg.png");
  }

  &__button {
    width: 13px;
    height: 13px;
    background-color: rgba(209, 115, 0, 0.5);
    box-shadow: inset 0px 2px 3px 0px rgba(51, 27, 2, 0.74);
    border-radius: 50%;
    border: none;
    cursor: pointer;
    transition: all 0.2s;
  }

  &__button-active-js {
    width: 12px;
    height: 12px;
    background-color: #7c5a4c;
    border: 1px solid #fff;
    border-radius: 50%;
    transition: all 0.2s;

    outline-offset: 1px;
    outline: 1px solid #7c5a45;
   }
}

.arrow {
  height: 324px;
  cursor: pointer;
  opacity: 0;
  z-index: 3;
}
.left {
  position: absolute;
  top:1px;
  left: 0px;
  transform: rotate(-180deg);
}
.right {
  position: absolute;
  top: 1px;
  right: 0px;
}

