.personal__gallery {
  max-width: 925px;
  min-height: 190px;
  padding: 0 60px;
  margin-bottom: 20px;
}

.gallery {
  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    gap: 25px ;
    align-items: center;

  }

  &__item {
    width: 182px;
    max-height: 189px;
    border: 1px solid #a08357;
    background-color: #ffffff;

    overflow: hidden;
    margin-bottom: 10px;

    display: flex;
    align-items: center;
    justify-content: center;
    }

  &__item img {
    height: 100%;
    object-fit: contain;
  }
}
