.table {
  &__wrapper {
    padding: 0 25px;
    background-color: #fff7e1;
    background-image: url("../../img/index/bg.png");
  }
}
.personal {
  &__table {
    border-collapse: collapse;
    font-family: Verdana, sans-serif;
  }

  &__content {
    font-size: 15px;
    line-height: 15px;
    height: 32px;
    text-align: center;
  }

  &__name {
    min-width: 350px;
    text-align: left;
    padding-left: 5px;
  }

  &__post {
    min-width: 230px;
  }

  &__age {
    min-width: 135px;
  }

  &__experience {
    min-width: 155px;
    text-align: right;
    padding-right: 55px;
  }
}

.title {
  &__cell {
    text-transform: uppercase;
    font-size: 15px;
    color: #ffffff;
    background-color: #814b04;
    text-align: center;
    border: 1px solid #d3a86c;
    height: 29px;
    line-height: 15px;
    text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
  }
}

.personal__content:nth-child(2n) {
  background-color: rgba(0, 0, 0, 0);
}

.personal__content:nth-child(2),
.personal__content:nth-child(5){
  color: #814b04;
}

.personal__content:nth-child(2n + 1) {
  background-color: #fff;
  border: 1px solid #d3a86c;
}

.personal__content:nth-child(2n) .personal__name,
.personal__content:nth-child(2n) .personal__post,
.personal__content:nth-child(2n) .personal__age {
  border-right: 1px solid #d3a86c;
}

.personal__content:last-child {
  border: 1px solid #d3a86c;
}
