.article {
  width: 925px;
  min-height: 400px;
  padding: 0px 24px;

  padding-bottom: 105px;

}

.article h3 {
  font-size: 24px;
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 10px;
  color: #4e2c1e;
}
.article p {
  font-size: 14px;
  margin-bottom: 22px;
  color: #4e2c1e;

  text-align: justify;
  word-spacing: 11px;
  line-height: 1.1;

}

.article p:last-of-type{
  margin-bottom: 0;
  word-spacing: 0px;
  text-align-last: justify;
}
