.request {
  &__container {
    width: 322px;
    min-height: 340px;

    padding: 13px 5px;

    border: 1px solid #c98b2f;
    border-radius: 3px;

    background-color: rgba(228, 221, 197, 0.89);

    font-family: Verdana, sans-serif;
    font-size: 14px;
    text-align: center;

    position: absolute;
    z-index: 10;
    top: 30px;
    right: 0;

    display: none;
  }
}

.resume {
  &__container {
    width: 322px;
    min-height: 294px;

    padding: 13px 5px;

    border: 1px solid #c98b2f;
    border-radius: 3px;

    background-color: rgba(228, 221, 197, 0.89);

    font-family: Verdana, sans-serif;
    font-size: 14px;
    text-align: center;

    position: absolute;
    z-index: 10;
    top: 65px;
    right: 0;
    z-index: 10;

    display: none;
  }
}

.form {
  &__client {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 13px;
    height: 100%;
  }
  &__resume {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 13px;
    height: 100%;
  }

  &__input {
    width: 100%;
    height: 32px;

    font-size: 14px;
    line-height: 1.2;
    background-color: #fff;

    color: #55493f;

    border: 1px solid #cd8e31;
    padding: 10px;
  }

  &__select {
    width: 100%;
    min-height: 32px;

    position: relative;

    background-color: #fff;

    border: 1px solid #cd8e31;
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding-left: 10px;
  }

  &__current {
    font-family: Verdana;
    font-size: 14px;
    line-height: 1.2;
    color: #55493f;
  }

  &__icon {
    background-image: url("../img/staff/select.png");
    background-repeat: no-repeat;
    background-position: right;

    width: 28px;
    height: 32px;
  }

  &__select-body {
    position: absolute;
    left: -1px;

    top: 32px;
    z-index: 2;

    width: calc(100% + 1.75px);

    background-color: #fcebc6;
    border: 1px solid #cd8e31;

    overflow: hidden;
    cursor: pointer;

    display: none;
  }

  &__item {
    height: 30px;
    width: 100%;

    // font-size: 14px;
    font-family: Verdana;
    line-height: 26px;
    color: #45250a;
    text-align: left;

    background-color: #fcebc6;
    padding: 2px 12px;

    transition: all 0.3s;
    cursor: pointer;
  }
  &__item:hover {
    background-color: #d8af75;
    transition: all 0.3s;
  }

  &__text {
    width: 100%;
    height: auto;
    min-height: 74px;
    background-color: #fff;
    color: #55493f;
    border: 1px solid #cd8e31;
    font-size: 14px;
    padding: 10px;
    line-height: 1.2;
    resize: none;
  }

  &__button {
    display: block;


    min-width: 144px;
    padding: 3px 27px;

    background: linear-gradient(
      to top,
      rgb(164, 103, 31) 0%,
      rgb(131, 76, 11) 14%,
      rgb(119, 71, 11) 25%,
      rgb(176, 120, 43) 55%,
      rgb(233, 192, 135) 100%
    );

    color: #ffffff;

    border: 1px solid #ab6e17;
    border-radius: 4px;

    font-size: 14px;
    font-family: Verdana, sans-serif;
    text-transform: uppercase;

    transition: all 0.3s;
    cursor: pointer;
  }
}

.request__container button{
  margin-top: 17px;
}

.resume__container button{
  margin-top: 15px;
}







