  &__file {
  }
}
.file {
  &__lable {
    width: 306px;
    height: 32px;
    opacity: 1;
    background-color: #fff;
    color: #55493f;
    border: 1px solid #cd8e31;

    font-size: 14px;
    padding-left:10px;
    line-height: 28px;
    display: block;
