@import "_nullstyle";

@import "fonts";

@import "./header/header.scss";

@import "./mainIndex/mainIndex.scss";
@import "./mainStaff/mainStaff.scss";

@import "./mainStaff/mainStaff.scss";
@import "./footer/footer.scss";

body {
  color: #4e2c1e;
  font-size: 13px;
  line-height: 16px;
  font-family: Arial, sans-serif;
  background-image: url("../img/bg-img.png");
}

.wrapper {
  min-width: 100%;
  margin: 0 auto;
}

.container-shadow {

  width: 924px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-image: url("../img/index/bg.png");
}

.main {
  flex-grow: 1;
  background-color: #fffae8;
  box-shadow: -12px 0px 10px -8px rgba(34, 60, 80, 0.2),
    12px 0px 10px -8px rgba(34, 60, 80, 0.2);
}

a {
  color: #8d8d8d;
  line-height: 26px;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}

.is-active-js {
  display: block;
}
